import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Manutencao_preventiva } from '../../../modelo/Manutencao_preventiva'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'manutencao_preventiva-dados-edit',   templateUrl: 'manutencao_preventiva_dadosEdit.component.html' }) export class Manutencao_preventivaDadosEditComponent implements OnInit {    manutencao_preventiva: Manutencao_preventiva;    private sub: Subscription;    private errorMessage: string;     constructor(private router: Router,               private route: ActivatedRoute,               private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.manutencao_preventiva.Manutencao_preventiva";                 let id = +params['id'];         this.manutencao_preventiva = new Manutencao_preventiva();         this.manutencao_preventiva.cd_manutencao_preventiva = id;         o.objetoJson = this.manutencao_preventiva;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a manutencao_preventiva. ';       }      this.manutencao_preventiva = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   }     onSubmit() {         }     onCancel(event: Event) {     event.preventDefault();     this.router.navigate(['/']);   }  }


